.latestmatch-container{
    display:flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 20px;
    background-color: black;
    color: white;
    font-family: 'Times New Roman';
    font-size: 2vw;
    margin: 5vw;
    margin-top: 0px;
    padding: 3vw;
}

.latestmatch-first{
    text-align: left;
}

.latestmatch-keys{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.competingteamlogo{
    width: 20vw;
    max-height:40vh;
}

.latestmatch-second{
    text-align: right;
}

