.home-bg-container{
    display: flex;
    background-image: url('https://assets.ccbp.in/frontend/react-js/ipl-dashboard-lg-bg.png');
    background-size: cover;
    width: 100vw;
    min-height: 100vh;
    background-repeat: no-repeat;
    flex-direction: column;
    /* overflow: hidden; */
}
.home-firstpart{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.ipl-heading{
    text-align: center;
    color: whitesmoke;
    font-size: 5vw;
    font-weight: bold;
}

.ipl-logo{
    margin-top: 1vw;
    width: 4vw;
    height: 9vh;
    padding-right: 20px;
}
