.matchcard-container{
    display: flex;
    justify-content: center;
    background-color: black;
    border:5px solid black;
    border-radius: 5%;
    color: white;
    margin:10px;
    width: 30%;
}

.matchcard-keys{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding:10px;
    font-size: 1.5vw;
}

.matchcard-logo{
    max-width:10vw;
    max-height:20vh;
}

