.teamMatches-redirect{
    display: flex;
    align-items: center;
    justify-content: space-around;
    text-decoration: none;
    margin:10px;
}

.teamcard{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 5%;
    padding: 3%;
    width: 230px;
    height: 100px;
    cursor: pointer;
    background-color: rgba(128, 128, 128, 0.4);
}

.team-icon{
    width: 8vw;
    height: 10vh;
}

.team-name{
    font-size: 2vw;
    text-align: center;
    color: white;
}